export default {
  install(Vue, options) {
    Vue.component('new-layout', () => import('../../components/layout/layout.vue'));
    Vue.component('video-header', () => import('../../components/header/videoHeader'));
    Vue.component('new-video-slider', () => import('../../components/video/slider.vue'));
    Vue.component('new-video', () => import('../../components/video/video.vue'));
    Vue.component('new-favourite', () => import('../../components/video/favourite/favourite.vue'));
    Vue.component('new-video-play', () => import('../../components/video/videoPlay.vue'));
    Vue.component('announce-header', () => import('../../components/header/announceHeader'));
    Vue.component('new-announce', () => import('../../components/announce/announce.vue'));
    Vue.component('new-announce-slider', () => import('../../components/announce/slider.vue'));
    Vue.component('new-recommend', () => import('../../components/announce/recommend/recommend.vue'));
  }
}



