import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: () => import('@/pages/home'),
      redirect: '/index',

      children:[
        {
          path: '/index',
          component: () => import('@/components/video/index')
        },
        {
          path: '/video',
          component: () => import('@/components/video/video')
        },

        {
          path:'/announce',
          component: ()=>import('@/components/announce/announce')
        },
        {
          path: '/encyclopedias',
          component: () => import('@/components/encyclopedias/encyclopedias')
        },
        {
          path: '/detail',
          component: () => import('@/components/encyclopedias/detail')
        }
        ,
        {
          path: '/assessment',
          component: () => import('@/components/assessment/assessment')
        }
        ,
        {
          path: '/nearsearch',
          component: () => import('@/components/assessment/nearsearch')
        }
        ,
        {
          path: '/nearsearchDetail',
          component: () => import('@/components/assessment/nearsearchDetail')
        }
        ,
        {
          path: '/assesscenter',
          component: () => import('@/components/assessment/assesscenter')
        }
      ]
    },

    {
      path: '/videoDetail',
      name: '视频详情页',
      component: () => import('@/pages/video/detail')
    },

    {
      path: '/announceDetail',
      name: '文章详情页',
      component: () => import('@/pages/announce/detail')
    }
  ]
})



