// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './assets/fonts/iconfont.css'
import '../static/css/main.css'
import Component from './assets/js/components.js'
import Utils from './assets/utils/functions'
import Vant from 'vant';
import 'vant/lib/index.css';
import Http from './assets/js/http' // ajax请求文件
import './assets/js/flex';
import Axios from 'axios';
// import './assets/js/rem'
import '../static/js/mui.min.js'
import VideoPlayer from 'vue-video-player'
require('video.js/dist/video-js.css');
require('vue-video-player/src/custom-theme.css');

import {
  Toast,
  Lazyload,
  PullRefresh
} from 'vant';

Vue.use(Toast)
.use(PullRefresh)
.use(Lazyload, {
  preLoad: 1,
  error: require('../static/imgs/error.png'), // 图片加载失败后的图片
  loading: require('../static/imgs/loading.gif'), // 图片加载完成前的图片
  attempt: 3
});

import { Search } from 'mint-ui';

Vue.component(Search.name, Search);
Vue.use(Vant);
Vue.use(Component);
Vue.use(Utils);
Vue.use(VideoPlayer);

 //  Vue.prototype.$HOST = 'http://172.16.11.35:8002';
Vue.prototype.$HOST = 'http://218.4.57.14:19103';

Vue.prototype.$http = Http;
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});



