import Dev from '../../../config/dev.env'
let IMG_ROOT = Dev.API_ROOT;
export default {
  install(Vue, options){

    /**
     * 获取图片链接
     */
     Vue.prototype.getImgURL = imgURL => {
      //IMG_ROOT = IMG_ROOT.replace('"', '');
       IMG_ROOT = IMG_ROOT.replace(/\"/g,'');
      if(IMG_ROOT){
        return  IMG_ROOT + '/file/' + imgURL;
      } else {
      }


    };



    //
    // getImgURL(fileId, resolution) {
    //   if (!fileId) return;
    //
    //   let url = resolution === undefined ? `${
    //     process.env.API_ROOT
    //     }/api/files/${fileId}/image` : `${
    //     process.env.API_ROOT
    //     }/api/files/${fileId}/image?resolution=${resolution}`;
    //
    //   // console.log('图片路径:');
    //   // console.log(url);
    //   return url;
    // }
    //
    //

    /**
     * yyyy-MM-dd
     * @param timer
     * @returns {string}
     */
    Vue.prototype.getYMD = (timer) => {
      var date = new Date(timer);
      var Y = date.getFullYear();
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
      var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      var allTime = Y + '-' + M + '-' + D;
      return allTime;
    };

    /**
     * yyyy年MM月dd日
     * @param timer
     * @returns {string}
     */
    Vue.prototype.getYMD2 = (timer) => {
      var date = new Date(timer);
      var Y = date.getFullYear();
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
      var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      var allTime = Y + '年' + M + '月' + D + '日';
      return allTime;
    };

    /**
     * yyyy-MM-dd hh:MM
     * @param timer
     * @returns {string}
     */
    Vue.prototype.getYMDHM = (timer) => {
      if (timer !== null) {
        var date = new Date(timer);
        var Y = date.getFullYear();
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
        var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        // var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        var allTime = Y + '-' + M + '-' + D + ' ' + h + ':' + m;
        return allTime;
      }
    };

    /**
     * 时间戳
     * yyyy/MM/dd hh:MM:ss
     * @param timer
     * @returns {string}
     */
    Vue.prototype.timeStamp = (timer) => {
      var date = new Date(timer);
      var Y = date.getFullYear();
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
      var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
      var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
      var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
      var allTime = Y + '/' + M + '/' + D ;
      return allTime;
    };

    /**
     * 获取当前时间
     * @returns {string}
     */
    Vue.prototype.getCurrentTime = () => {
      let date = new Date();
      return [date.getFullYear(), ((date.getMonth() + 1) < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1),
        (date.getDate() < 10 ? '0' + date.getDate() : date.getDate())].join('-') + '  ' + [(date.getHours() < 10 ? '0' + date.getHours() : date.getHours()),
        (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()),
        (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds())].join(':');
    };


    /**
     * 保留两位小数
     * 参数：num
     * 数据类型：Number
     */
    Vue.prototype.fixedTwo = num => {
      return num.toFixed(2);
    }
  }
};



